import {GETGOODS,ADDCAR,GETGOODSCOUNT,GETGOODSCAR,ADDREDUCE,DELONE} from "../actions/index";

const defaultState = {
    goods:[],
    tabs:[
        { title: '' },
        { title: '' },
        { title: '' },
        { title: '' },
        { title: '' },
        { title: '' },
        { title: '' },
    ],
    addcarmsg:"",
    carNum:0,
    goodsCar:[],
    total:0, //
    quan:false, //
    dingdan:[]
}

export default (state=defaultState,action)=>{
    console.log(action);

    switch (action.type){
        case GETGOODS:
        return {...state,goods:action.goods};
        break;

        case ADDCAR:
        return {...state,addcarmsg:action.addcarmsg};
        break;

        case GETGOODSCOUNT:
        return {...state,carNum:action.carNum};
        break;

        case GETGOODSCAR:
        return {...state,goodsCar:action.goodsCar};
        break;

        case ADDREDUCE:
        return {...state,goodsCar:action.goodsCar};
        break;

        case DELONE:
        return {...state,goodsCar:action.goodsCar};
        break;

        case "setCar": 
        //arr
        var arr  = state.goodsCar.map((item)=>{
            //goodidactiongoodidcheck
            if(item.goodid==action.goodid){
                item.check = action.checked;
            }
            return item;
        });
        var total = 0;
        arr.forEach((item)=>{  //
            if(item.check){ //
                total+=item.goodprice*item.num;
            }
        })
        //false
        var flag = true; //
        arr.forEach((item)=>{
            if(!item.check){
                flag = false;
            }
        })
        return {...state,goodsCar:arr,total:total.toFixed(2),quan:flag};
        break;
        
        //
        case "setQuan":
        var total = 0;
        let arr  = state.goodsCar.map(item=>{
            item.check = action.checked; //
            if(item.check){
                total+=item.goodprice*item.num;
            }
            return item;
        })
        return {...state,quan:action.checked,goodsCar:arr,total:total.toFixed(2)}
        break;

        case "WillMount":
        var total = 0;
        console.log(action.goodsCar)
        action.goodsCar.map((item,index)=>{
            if(item.check){
                total+=item.goodprice*item.num;
            }
            return item;
        })
        var flag = true; //
        action.goodsCar.forEach((item)=>{
            if(!item.check){
                flag = false;
            }
        })
        return {...state,total:total.toFixed(2),quan:flag};
        break;

        default:
        return state;
        break;
    }
}


