import "./index.scss";
import React, {Component}  from "react";

import {  WhiteSpace, WingBlank ,Button,List, Toast} from 'antd-mobile';
import Head from "../../components/head";

import mui from './mui.min.js';
import init from './init.js';

import axios from "axios";
//
//axios.defaults.baseURL = "http://localhost:3200";  // 
axios.defaults.baseURL = "http://123.56.221.140:3200";  // 

export default class Login extends Component{

    state={
        username:"",
        pass:"",
        disabled:true
    }

    change=()=>{
        this.setState({
            username:this.refs.username.value,
            pass:this.refs.pwd.value
        })

        let usernameReg=/^[\u4e00-\u9fa5]{2,4}$/ig;
        let pwdReg=/^[a-z0-9]{6,8}$/ig;

        if(usernameReg.test(this.refs.username.value)&&pwdReg.test(this.refs.pwd.value)){
            this.setState({
                disabled:false
            })
            }else{
            this.setState({
                disabled:true
            })
        }
    }

    login(){
        axios.post("/vue/reactLogin",{
            params:{
                username:this.state.username,
                pass:this.state.pass
            }
        }).then(res=>{
            if(res.data.type==1){
                Toast.success('!', 0.8);
                setTimeout(()=>{
                    this.props.history.push("/app/home");
                    sessionStorage.username=this.state.username
                },800)   
            }else{
                Toast.fail(res.data.msg, 2);
                this.refs.username.value="",
                this.refs.pwd.value=""
            }
        }) 
    }
    
    getAuth=(index)=>{
    	var auths = '';
    	plus.oauth.getServices(
					(services) => {
						auths = services; //QQ
						console.log(JSON.stringify(auths)); //
						var s = auths[index];
						if(!s.authResult) {
					s.login(function(e) {
						alert("");
						s.getUserInfo(function(e) {
							alert("" + JSON.stringify(s.userInfo));
							console.log(s.userInfo.nickname); //
						}, function(e) {
							alert("" + e.message + " - " + e.code);
						});
					}, function(e) {
						alert("");
					});
				} else {
					alert("");
				}
					},
					(e) => {
						alert("" + e.message + " - " + e.code);
					}
				)	
    }
    

    render(){
        return (
            <div className="box">
                <Head title="" show={true} history={this.props.history} ></Head>
                <WingBlank>
                    <List>
                        <div className="username">
                        <label htmlFor="">
                         :<input
                            type="text"
                            ref="username"
                            placeholder=""
                            onChange={this.change}   
                        />
                        </label>
                        </div>

                        <div className="pwd">
                        <label htmlFor="">
                           :<input
                            type="password"
                            ref="pwd"
                            placeholder=""
                            onChange={this.change}
                        />
                        </label>
                        </div> 
                    </List>  
                    <Button type="primary" onClick={()=>{this.login()}} disabled={this.state.disabled}></Button>  
                </WingBlank>  
                <Button type="primary" onClick={()=>this.getAuth(0)} >QQ</Button> 
                <Button type="primary" onClick={()=>this.getAuth(2)} ></Button> 
                <Button type="primary" onClick={()=>this.getAuth(3)} ></Button>
            </div>
        )
    }
}


